.wdb-author-box {
  display: flex;
  align-items: center;
}

.wdb-author-box__avatar {
  flex-shrink: 0;
  margin-right: 25px;
}

.wdb-author-box__avatar img {
  width: 100px;
  border-radius: 500px;
  -o-object-fit: cover;
  object-fit: cover;
}

.wdb-author-box__text {
  flex-grow: 1;
  font-size: 17px;
}

.wdb-author-box__name {
  font-size: 24px;
  margin-bottom: 5px;
}

.wdb-author-box__bio,
.wdb-author-box__bio p {
  margin-bottom: 0.8em;
}

.wdb-author-box__bio p:last-of-type {
  margin-bottom: 0;
}

.wdb-author-box--layout-image-left .wdb-author-box {
  flex-direction: row;
}

.wdb-author-box--layout-image-left .wdb-author-box__avatar {
  margin-left: 0;
  margin-right: 25px;
}

.wdb-author-box--layout-image-right .wdb-author-box {
  flex-direction: row-reverse;
}

.wdb-author-box--layout-image-right .wdb-author-box__avatar {
  margin-right: 0;
  margin-left: 25px;
}

.wdb-author-box--layout-image-above .wdb-author-box {
  display: block;
}

.wdb-author-box--layout-image-above .wdb-author-box__avatar {
  margin: 0 auto 15px;
  display: inline-block;
}

.wdb-author-box--layout-image-above.wdb-author-box--align-left .wdb-author-box__avatar {
  margin-left: 0;
}

.wdb-author-box--layout-image-above.wdb-author-box--align-right .wdb-author-box__avatar {
  margin-right: 0;
}

.wdb-author-box--align-left .wdb-author-box {
  text-align: left;
}

.wdb-author-box--align-right .wdb-author-box {
  text-align: right;
}

.wdb-author-box--align-center .wdb-author-box {
  text-align: center;
}

.wdb-button.wdb-author-box__button {
  border: 2px solid #3f444b;
  font-size: 15px;
  font-weight: 100;
  padding: 10px 18px;
}

.wdb-button.wdb-author-box__button, .wdb-button.wdb-author-box__button:hover {
  background: transparent;
  color: #3f444b;
}

.elementor-widget-wdb--author-box:not(.wdb-author-box--layout-image-above).wdb-author-box--image-valign-top .wdb-author-box__avatar,
.elementor-widget-wdb--author-box:not(.wdb-author-box--layout-image-above) .wdb-author-box__avatar {
  align-self: flex-start;
}

.elementor-widget-wdb--author-box:not(.wdb-author-box--layout-image-above).wdb-author-box--image-valign-middle .wdb-author-box__avatar {
  align-self: center;
}